import styled from 'styled-components';
import copy from 'copy-to-clipboard';

import { Header, redColor, Subtitle, Title } from '../../css-in-js/global';
import {  getToken, revokeAccessToken } from '../../services/token';
import { Button, Content, UserData, UserDataItem } from '../Profile/Profile.style';
import { ErrorPin, InformationPin, PinContainer } from '../Start/start.style';
import { IconCopy } from './components/IconCopy';
import { IconWarning } from './components/IconWarning';

const RedTitle = styled(Title)`
  color: ${redColor};
`;

export function Token() {
  const copyToken = () => {
    const token = getToken();
    if (token) copy(token);
    return;
  };

  const copyLink = () => {
    const token = getToken();
    const link = `${window.location.origin}/start/${token}`;
    if (token) copy(link);
    return;
  };

  return (
    <>
      <Header>
        <RedTitle></RedTitle>
        <Subtitle> </Subtitle>
      </Header>

      <UserData>
        <UserDataItem>
          <Content>
            <Button onClick={copyToken}>
              <IconCopy />
            </Button>
            <p> </p>
          </Content>

          <PinContainer>
            <InformationPin>
                 ,     
            </InformationPin>
          </PinContainer>
        </UserDataItem>

        <UserDataItem>
          <Content>
            <Button onClick={copyLink}>
              <IconCopy />
            </Button>
            <p>   </p>
          </Content>

          <PinContainer>
            <InformationPin>   </InformationPin>
          </PinContainer>
        </UserDataItem>

        <UserDataItem>
          <Content>
            <Button onClick={revokeAccessToken}>
              <IconWarning />
            </Button>
            <p> </p>
          </Content>

          <PinContainer>
            <ErrorPin>     </ErrorPin>
          </PinContainer>
        </UserDataItem>
      </UserData>
    </>
  );
}
